import React from "react";
import emailjs from "@emailjs/browser";

function Form() {
  const handleFormSubmit = function (e) {
    e.preventDefault();
    var option;
    if (e.target.op1.checked) option = "تحديد نوع الجنين";
    else if (e.target.op2.checked) option = "عﻻج تأخر الإنجاب";
    else if (e.target.op3.checked) option = "الفحوصات الوراثية";
    else if (e.target.op4.checked) option = "دوالي الخصية";
    else if (e.target.op5.checked) option = "عﻻج ضعف الإنتصاب";
    else if (e.target.op6.checked) option = "دعامة القضيب";
    else alert("من فضلك ادخل جميع البيانات");

    if (!e.target.name.value) alert("من فضلك ادخل جميع البيانات");
    if (!e.target.phone.value) alert("من فضلك ادخل جميع البيانات");

    const name = e.target.name.value;
    const phone = e.target.phone.value;

    console.log(option);

    emailjs
      .send(
        "ganna-form",
        "template_nqpiuvw",
        { name, phone, option },
        "sAsd3y5bIPc2pggVw"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <div className="flex justify-center sm:mx-44 sm:my-22 m-16 font-bold text-right">
      <div className="mt-10 sm:mt-0">
        <h1 className="mb-10 flex justify-center">
          <div className="relative inline-flex items-center justify-center p-0.5 mb-2 mr-2 overflow-hidden text-sm font-medium text-gray-900 rounded-lg groupfocus:ring-4 focus:outline-none focus:ring-pink-200">
            <span className="text-4xl relative px-5  transition-all ease-in duration-75  font-bold text-indigo-900 rounded-md ">
              مستشفي جنة
            </span>
          </div>
        </h1>
        <span className="text-sm mb-4 flex justify-center transition-all ease-in duration-75  font-bold text-indigo-900 rounded-md ">
          علاج تاخر الإنجاب اهم رغبة لدي الزوج والزوجة وهو ما تسعي إليه مستشفي
          جنة .. إملا بياناتك الان وسيتواصل معك فريق الأطباء
        </span>

        <div className="mt-5 md:mt-0 md:col-span-2 max-w-2xl">
          <img alt="" src="./img/form_pic.jpeg" />
          <form name="myForm" onSubmit={handleFormSubmit} className="text-right">
            <div className="shadow sm:rounded-md sm:overflow-hidden">
              <div className="px-4 py-5  space-y-6 sm:p-6">
                <div className="col-span-6 sm:col-span-4">
                  <label
                    htmlFor="name"
                    className="block text-xl font-bold text-indigo-900"
                  >
                    <span className="text-red-400"> * </span> الاسم
                  </label>
                  <input
                    type="text"
                    name="name"
                    placeholder="الاسم بالكامل"
                    id="name"
                    autoComplete="name"
                    className= "text-right shadow-sm p-2 text-blue-900 focus:ring-indigo-500 focus:border-indigo-500 mt-1 block w-full sm:text-sm border border-gray-300 rounded-md"
                  />
                </div>
                <div className="col-span-6 sm:col-span-4">
                  <label
                    htmlFor="phone"
                    className=" block text-xl font-bold text-indigo-900"
                  >
                    <span className="text-red-400"> * </span> رقم الهاتف
                  </label>
                  <input
                    type="text"
                    name="phone"
                    placeholder="01234567890"
                    id="phone"
                    autoComplete="phone"
                    className="text-right shadow-sm text-blue-900   p-2 focus:ring-indigo-500 focus:border-indigo-500 mt-1 block w-full sm:text-sm border border-gray-300 rounded-md"
                  />
                </div>
                <fieldset className="	flex justify-end">
                  <legend className="block text-xl font-bold text-indigo-900">
                    <span className="text-red-400"> * </span>الخدمة المطلوبة
                  </legend>
                  <div className="mt-4 space-y-4">
                    <div className="flex items-center">
                      <label
                        htmlFor="op1"
                        className="mr-3 flex justify-end text-lg font-medium text-gray-700"
                      >
                        تـحديـد نـوع الجـنين
                      </label>
                      <input
                        id="op1"
                        name="op"
                        type="radio"
                        className="focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300 flex justify-end"
                      />
                    </div>

                    <div className="flex items-center">
                      <label
                        htmlFor="op2"
                        className="mr-3 flex justify-end text-lg font-medium text-gray-700"
                      >
                        عـلاج تأخر الإنجاب
                      </label>
                      <input
                        id="op2"
                        name="op"
                        type="radio"
                        className="focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300 flex justify-end"
                      />
                    </div>

                    <div className="flex items-center">
                      <label
                        htmlFor="op3"
                        className="mr-3 text-lg font-medium text-gray-700 flex justify-end"
                      >
                        الفحوصات الـوراثية
                      </label>
                      <input
                        id="op3"
                        name="op"
                        type="radio"
                        className="focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300 flex justify-end"
                      />
                    </div>

                    <div className="flex items-center">
                      <label
                        htmlFor="op4"
                        className="mr-3 flex justify-end text-lg font-medium text-gray-700"
                      >
                        دوالـــي الـخــصــيـة
                      </label>
                      <input
                        id="op4"
                        name="op"
                        type="radio"
                        className="focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300 flex justify-end"
                      />
                    </div>

                    <div className="flex items-center">
                      <label
                        htmlFor="op5"
                        className="mr-3 flex justify-end text-lg font-medium text-gray-700"
                      >
                         ضـعـف الإنـتـصـاب
                      </label>
                      <input
                        id="op5"
                        name="op"
                        type="radio"
                        className="focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300 flex justify-end"
                      />
                    </div>

                    <div className="flex items-center">
                      <label
                        htmlFor="op6"
                        className="mr-3 flex justify-end text-lg font-medium text-gray-700"
                      >
                        دعــامــة الـقـضـيــب 
                      </label>
                      <input
                        id="op6"
                        name="op"
                        type="radio"
                        className="focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300 flex justify-end"
                      />
                    </div>
                  </div>
                </fieldset>
              </div>
              <div className="px-4 py-3 bg-gray-50 text-right sm:px-6">
                <button
                  type="submit"
                  className="inline-flex justify-center py-2 px-4 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                >
                  Submit
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>

      <div className="hidden sm:block" aria-hidden="true">
        <div className="py-5">
          <div className="border-t border-gray-200" />
        </div>
      </div>
    </div>
  );
}

export default Form;
