// Libraries
import React from "react";

// Images
import hallstatt from "./backgrounds/1.jpeg";
import hvitserkur from "./backgrounds/2.jpeg";
import jacksonville from "./backgrounds/3.jpeg";
import moraineLake from "./backgrounds/4.jpg";
import * as Scroll from "react-scroll";

// Components
import HeroSlider, {
  Slide,
  ButtonsNav,
  Nav,
  OverlayContainer,
} from "hero-slider";
import Subtitle from "../UI/Subtitle/Subtitle";

let scroll = Scroll.animateScroll;

const ZoomSlider = () => {
  const nextSlideHandler = React.useRef();
  const previousSlideHandler = React.useRef();

  const handleScrollToForm = function () {
    scroll.scrollToBottom();
  };

  return (
    <HeroSlider
      nextSlide={nextSlideHandler}
      previousSlide={previousSlideHandler}
      orientation="horizontal"
      initialSlide={3}
      onBeforeChange={(previousSlide, nextSlide) =>
        console.log("onBeforeChange", previousSlide, nextSlide)
      }
      onChange={(nextSlide) => console.log("onChange", nextSlide)}
      onAfterChange={(nextSlide) => console.log("onAfterChange", nextSlide)}
      style={{
        backgroundColor: "#fff",
      }}
      settings={{
        slidingDuration: 600,
        isSmartSliding: true,
        slidingDelay: 50,
        shouldAutoplay: true,
        shouldDisplayButtons: true,
        autoplayDuration: 1000,
        height: "100vh",
      }}
    >
      <OverlayContainer className="flex items-start justify-center mt-32 sm:mt-16">
        <div className="max-w-4xl bg-black opacity-60 rounded-full ">
          <Subtitle>لفترة محدودة خصم 20% علي الحقن المجهري </Subtitle>
        </div>
      </OverlayContainer>

      <OverlayContainer className="flex items-start justify-center mt-16">
        <button
          className="mt-96 sm:mt-32 text-white bg-gray-800 hover:bg-gray-900 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-2xl  px-5 py-2.5 mr-2 mb-2"
          onClick={handleScrollToForm}
        >
          سجل اﻵن
        </button>
      </OverlayContainer>

      <OverlayContainer className="flex items-start justify-end">
       
        <img alt="" src="./img/logo.png" width="100" height="100" className="mr-10 mt-6"/>
      </OverlayContainer>

      <Slide
        navDescription="الأمراض الوراثية"
        background={{
          backgroundImage: hallstatt,
          backgroundAnimation: "zoom",
        }}
      />

      <Slide
        navDescription="قسم النساء والتوليد"
        background={{
          backgroundImage: hvitserkur,
          backgroundAnimation: "zoom",
        }}
      />

      <Slide
        navDescription="الحقن المجهري"
        background={{
          backgroundImage: jacksonville,
          backgroundAnimation: "zoom",
        }}
      />

      <Slide
        navDescription="أمراض الذكور"
        background={{
          backgroundImage: moraineLake,
          backgroundAnimation: "zoom",
        }}
      />

      <ButtonsNav
        isNullAfterThreshold
        position={{
          top: 0,
          left: "50%",
          transform: "translateX(-50%)",
        }}
      />
      <Nav />
    </HeroSlider>
  );
};

export default ZoomSlider;
