import React from "react";

function YTVideo() {
  return (
    <div className="sm:mx-44 sm:my-22 m-16">
      <h1 className="mb-10 flex justify-center">
        <div class="relative inline-flex items-center justify-center p-0.5 mb-2 mr-2 overflow-hidden text-sm font-medium text-gray-900 rounded-lg group  focus:ring-4 focus:outline-none focus:ring-pink-200">
          <span class="text-4xl relative px-5 py-2.5 transition-all ease-in duration-75 bg-white font-bold text-indigo-900 rounded-md ">
            من نحن ؟
          </span>
        </div>
      </h1>

      <div
        className="video"
        style={{
          position: "relative",
          paddingBottom: "56.25%" /* 16:9 */,
          paddingTop: 25,
          height: 0,
        }}
      >
        <iframe
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
          }}
          src="https://www.youtube.com/embed/e0gZYsybPTQ"
          title="YouTube video player"
          frameBorder="0"
        />
      </div>
    </div>

    // <div>
    //   <iframe
    //     width="560"
    //     height="315"
    //     src="https://www.youtube.com/embed/e0gZYsybPTQ"
    //     title="YouTube video player"
    //     frameborder="0"
    //     allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
    //     allowfullscreen
    //   ></iframe>
    // </div>
  );
}

export default YTVideo;
